


















import './record-read.scss';
import { IRecord } from '@/core/interfaces/record.interface';
import { IComponent } from '@/core/interfaces/component.interface';
import { IRecordRead } from './record-read.interface';

export default {
  computed: {
    record(): IRecord {
      const editPage = this as unknown as IComponent;
      return editPage.$store.getters.record(editPage.$route.params?.id);
    },

    sectorTitle(): string {
      const editPage = this as unknown as IRecordRead;
      return editPage.$store.getters.sectorTitle(editPage.record?.id);
    },
  },

  methods: {
    async closeBlank(): Promise<void> {
      const editPage = this as unknown as IRecordRead;
      await editPage.$router.push('/main/admin');
    },
  },

};
